.registerPage{
  height:500px;
  background:-webkit-gradient(linear,left top, left bottom,from(rgb(97, 201, 144)),to(rgb(207, 149, 40)));
  background:-webkit-linear-gradient(rgb(97, 201, 144),rgb(207, 149, 40));
  background:-o-linear-gradient(rgb(97, 201, 144),rgb(207, 149, 40));
  background:linear-gradient(rgb(97, 201, 144),rgb(207, 149, 40));
  padding-top:1rem;
}
.registerPage h1{
  text-align: center;
  font-family: 'Lobster', cursive;
  margin-top:0;
  padding-top:1rem;
}
.wrap{
  width:40%;
  height:400px;
  background:-webkit-gradient(linear,left top, left bottom,from(rgb(97, 201, 144)),to(rgb(207, 149, 40)));
  background:-webkit-linear-gradient(rgb(97, 201, 144),rgb(207, 149, 40));
  background:-o-linear-gradient(rgb(97, 201, 144),rgb(207, 149, 40));
  background:linear-gradient(rgb(97, 201, 144),rgb(207, 149, 40));
  -webkit-box-shadow: 1px 2px 2px  black;
          box-shadow: 1px 2px 2px  black;
  margin:0 auto;
  border-radius:5px;
  margin-top:1rem;
}
.registerPage label{
  display:block;
  margin:10px 0 0 1rem;
}
.registerPage input{
  display:block;
  margin:10px auto;;
  width:80%;
  border-radius:4px;
  border:none;
  height:2rem;
}
.registerPage h5{
  text-align: center;
}
.registerPage a:active,
.registerPage a:hover{
  color:white;
}

.registerPage input[type="submit"]{
  background:tomato;
  width:40%;
  border:none;
  padding:10px;
  margin:10px auto;
  cursor: pointer;
  color:white;
  -webkit-box-shadow:1px 1px 1px black;
          box-shadow:1px 1px 1px black;
  font-size:1rem;
}
.registerPage input[type="submit"]:hover{
-webkit-transform:scale(1.04);
    -ms-transform:scale(1.04);
        transform:scale(1.04)
}
@media(max-width:700px){
  .wrap{
    width:70%;
    margin-left:25%;
  }
}


