   border:2px solid black;
   padding: 4px;
   margin-bottom:3rem;
}

.submitButton{
  border:none;
  color:white;;
  background:#E93925;
  cursor:pointer;
  font-size:1rem;
  -webkit-box-shadow: 1px 1px 1px black;
          box-shadow: 1px 1px 1px black;
  border-radius:5px ;
  padding: 0.5rem 3rem;

}
.submitButton:hover{
  -webkit-transform:scale(1.02);
      -ms-transform:scale(1.02);
          transform:scale(1.02);

}
@media(max-width:450px){
  .BarcodeInput{
    width:60%
  }
}


