.CalorieApp{
  /* background:linear-gradient(rgb(97, 201, 144),rgb(40, 168, 207)); */
  padding-top:3rem;
  background:-webkit-gradient(linear,left top, left bottom,from(rgb(18, 19, 18)),to(rgb(40, 168, 207)));
  background:-webkit-linear-gradient(rgb(18, 19, 18),rgb(40, 168, 207));
  background:-o-linear-gradient(rgb(18, 19, 18),rgb(40, 168, 207));
  background:linear-gradient(rgb(18, 19, 18),rgb(40, 168, 207));
  min-height: 70vh !important;

}
.CalorieApp h1{
  margin:0;
  padding:1rem 0;
  text-align: center;
  font-family: 'Lobster', cursive;
  color:white;
}
.CalorieApp p{
  text-align: center;
  color:white;
  font-weight: normal;
}
.BarcodeForm{
  text-align: center;
}
.BarcodeInput{
   width:40%;
   height:1.5rem;
   margin:1rem auto;
}
.submitButton{
  border:none;
  padding:1rem;
  color:white;;
  background:tomato;
  cursor:pointer;
  font-size:1.2rem;
}
.productImg{
  display:block;
  margin:0 auto;
  max-width: 100%;
  max-height:300px;


}
.productImgNutrition{
  display:block;
  margin:1rem auto;
  max-width: 100%;
  max-height:150px;
}
p{
  /* font-weight:bold; */
  text-align: center;
  margin-right:2rem;

}
.Info img{border: 3px solid grey;
  max-width:100%;

}
.Info{
  margin:1rem auto 0;
  width:80%;
  height:600px;
  background:#030b18;
  -webkit-box-shadow: 6px 4px 11px #000000b5;
          box-shadow: 6px 4px 11px #000000b5;
  color:white;
  padding:1rem;
}
.Info ul{
  display:block;
  margin:0 35%;

}
.Info li{
  list-style: none;
  margin:0 auto;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: justify;
      justify-content: space-between;

}
.Info li span{
  color:dodgerblue;
  font-weight:700;
}
.name{
  font-size:1.5rem;
  font-family:'Lobster',cursive;
}
.HowTo{
  max-width: 100%;
  display:block;
  margin:0 auto;
  width:200px;
  height:200px;
  -webkit-transition: all 0.5s ease-in;
  -o-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in;
}
/* .HowTo:hover{
  transform: scale(1.05);
  filter:hue-rotate(20deg)
} */
.description{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-wrap:wrap;
      flex-wrap:wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin-top: 4rem;
}
.description div{
  width:300px;
}
.description div p{
 text-align: center;
 margin-left:2rem;
}
.description div img{
 max-width:100%;
 height:auto;
 width:120px;
}

.easy {
  margin-left:3rem;
}
.error{
  background-color:#E93925;
  color:white;
  text-align: center;
  height:3rem;
  font-size:1rem;
  padding:0.5rem;
}
.imgDiv{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-wrap:wrap;
      flex-wrap:wrap;
}
