.footer{
  margin-top:-1rem;
  /* background: #222222; */
  background:-webkit-gradient(linear,left top, left bottom,from(rgb(6, 46, 58)),to(rgb(10, 56, 70)));
  background:-webkit-linear-gradient(rgb(6, 46, 58),rgb(10, 56, 70));
  background:-o-linear-gradient(rgb(6, 46, 58),rgb(10, 56, 70));
  background:linear-gradient(rgb(6, 46, 58),rgb(10, 56, 70));
  color:white;
  padding-top:2rem;


}
/* .Logo{
  width:50px;
  max-width:100%;
  height:auto;
} */
.footer p{
  /* padding-top:1rem; */
  margin-left:2rem;
  text-align:center;
}
.footer ul{
  text-align: center;
  margin:1rem auto;
  width:200px;
  padding-right:35px;

}
.footer li{
  list-style: none;

}
.footer a{
  text-decoration: none;
  color:rgb(22, 212, 95);
}
.footer li a:hover{
  color:tomato;
}
.footer img{
  max-width: 100%;
  width:200px;
  display:block;
  margin:0 auto;
}
.footer .logo{
  width:100px;
  margin:0 auto;
}
@media(max-width:1000px){
  .footer{
    margin-left:4.1rem;
  }
  .footer p{
    font-size:.8rem;
  }
  .footer img{
    max-width: 100%;
    width:250px;
    display:block;
    margin:0 auto;
  }
}
@media(max-width:500px){
  .footer{
    padding-left:1rem;
    padding-right:2rem;
  }
  .footer p{
    font-size:.8rem;
  }
  .footer img{
    max-width: 100%;
    width:120px;
    display:block;
    margin:0 auto;
  }
  .footer a{
    font-size:0.8rem;
  }
}

@media(max-width:600px){
  .footer .logo{
    margin:0 auto;
  }
}
@media(max-width:400px){
  .footer .logo{
    margin:0 32%;
  }
}


