.header {
  /* background-color: #222; */
  background:-webkit-gradient(linear,left top, left bottom,from(rgb(11, 69, 87)),to(rgb(28, 30, 30)));
  background:-webkit-linear-gradient(rgb(11, 69, 87),rgb(28, 30, 30));
  background:-o-linear-gradient(rgb(11, 69, 87),rgb(28, 30, 30));
  background:linear-gradient(rgb(11, 69, 87),rgb(28, 30, 30));
  height: 60px;
  padding:10px;
  color: white;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
nav{
  width:80%;
  max-height:80%;
}
.navElements{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.navElements li{
  font-family: 'Lato', cursive;
  list-style: none;
  margin:1.2rem 4rem;
}
.navElements a{
  text-decoration: none;
  color:white;
  font-size:1.2rem;
}

.navElements a:hover{
  color:tomato;

  border-bottom:2px solid tomato;
  padding-bottom:0.6rem;
}
@media(max-width: 1000px ){

  .navElements{
    z-index:1000;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction: column;
        flex-direction: column;
    position: absolute;
    position:fixed;
    top:4rem;
    left:0;
    background:red;
    width:3rem;
    height:100vh;
    background:-webkit-gradient(linear,right top, left top,from(rgb(34, 170, 163)),to(rgb(40, 168, 207)));
    background:-webkit-linear-gradient(right,rgb(34, 170, 163),rgb(40, 168, 207));
    background:-o-linear-gradient(right,rgb(34, 170, 163),rgb(40, 168, 207));
    background:linear-gradient(to left,rgb(34, 170, 163),rgb(40, 168, 207));
    -webkit-box-shadow: 2px 2px 2px  black;
            box-shadow: 2px 2px 2px  black;
    -ms-flex-pack: initial;
        justify-content: initial;
  }
  .navElements li{
    list-style: none;
    padding:0;
    margin:1rem -2.4rem 8rem;

  }
  .navElements a{
    text-decoration: none;
    color:black;
    font-size:1rem;
    text-align: center;
    font-weight:800;
    padding-left:3px;
  }
  .navElements a:hover{
    color:rgba(255, 255, 255, 0.61);
    padding-bottom:0.6rem;
    border-left:5px solid rgba(255, 255, 255, 0.61) ;
    border-bottom: none;
  }


}
@media(max-width: 500px){
  .navElements{
    width:1.5rem;
 }
 .navElements a{
   font-size:.85rem;
 }
}



