.loginPage{
  height:500px;
  background:-webkit-gradient(linear,left top, left bottom,from(rgb(97, 201, 144)),to(rgb(40, 168, 207)));
  background:-webkit-linear-gradient(rgb(97, 201, 144),rgb(40, 168, 207));
  background:-o-linear-gradient(rgb(97, 201, 144),rgb(40, 168, 207));
  background:linear-gradient(rgb(97, 201, 144),rgb(40, 168, 207));
  padding-top:1rem;
}
.loginPage h1{
  text-align: center;
  font-family: 'Lobster', cursive;
  margin-top:0;
  padding-top:1rem;
}
.wrap{
  width:40%;
  height:350px;
  background:-webkit-gradient(linear,left top, left bottom,from(rgb(97, 201, 144)),to(rgb(124, 181, 214)));
  background:-webkit-linear-gradient(rgb(97, 201, 144),rgb(124, 181, 214));
  background:-o-linear-gradient(rgb(97, 201, 144),rgb(124, 181, 214));
  background:linear-gradient(rgb(97, 201, 144),rgb(124, 181, 214));
  -webkit-box-shadow: 1px 2px 2px  black;
          box-shadow: 1px 2px 2px  black;
  margin:0 auto;
  border-radius:5px;
  margin-top:1rem;
}
.loginPage label{
  display:block;
  margin:10px 0 0 1rem;
}
.loginPage input{
  display:block;
  margin:10px auto;;
  width:80%;
  border-radius:4px;
  border:none;
  height:2rem;
}
.loginPage h5{
  text-align: center;
}
.loginPage a:active,
.loginPage a:hover{
  color:white;
}

.loginPage input[type="submit"]{
  background:tomato;
  width:40%;
  border:none;
  padding:10px;
  margin:2rem auto;
  cursor: pointer;
  color:white;
  -webkit-box-shadow:1px 1px 1px black;
          box-shadow:1px 1px 1px black;
  font-size:1rem;
}
.loginPage input[type="submit"]:hover{
-webkit-transform:scale(1.04);
    -ms-transform:scale(1.04);
        transform:scale(1.04)
}
@media(max-width:700px){
  .wrap{
    width:70%;
    margin-left:25%;
  }
}


